"use client";

import { useState, useMemo, useEffect } from 'react';
import { format } from 'date-fns';
import { CalendarDays, CheckCircle2, Settings2, Banknote, ExternalLink } from 'lucide-react';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Checkbox } from '@/components/ui/checkbox';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { RecordPaymentDialog } from './RecordPaymentDialog';
import { cn } from '@/lib/utils';

// Update this interface to match what you are passing from page.tsx
interface BillingTableProps {
  bills: any[];
  onProjectNavigate: (id: number) => void;
  onRefresh: () => void; // This matches your fetchBills function
}

export function BillingTable({ bills, onProjectNavigate, onRefresh }: BillingTableProps) {
  const [visibleColumns, setVisibleColumns] = useState({
    client: true,
    department: true,
    received: true
  });
  return (
    <div className="glass-card rounded-2xl overflow-hidden border border-border/50">
      <div className="overflow-x-auto">
        <table className="w-full text-left border-collapse">
          <thead className="bg-muted/50 text-[10px] font-black uppercase text-muted-foreground">
            <tr>
              <th className="p-4">Milestone Detail</th>
              <th className="p-4">Schedule (Target vs Actual)</th>
              <th className="p-4 text-center">Allocation %</th>
              <th className="p-4">Financials (Due vs Rec)</th>
              <th className="p-4">Status</th>
              <th className="p-4 text-right">Link</th>
            </tr>
          </thead>
          <tbody className="text-sm">
            {bills.map((bill) => {
              const totalVal = Number(bill.project?.totalProjectValue || 0);
              const recAmt = Number(bill.receivedAmount || 0);
              const dueAmt = Number(bill.billAmount || 0);
              // Calculate effective percentage of total project received for this bill
              const recPct = totalVal > 0 ? ((recAmt / totalVal) * 100).toFixed(1) : "0";
              const targetPct = bill.billPercent || "0";

              return (
                <tr key={bill.id} className="border-b border-border/40 hover:bg-primary/[0.02] cursor-pointer group" onClick={() => onBillClick(bill)}>
                  <td className="p-4">
                    <p className="font-black text-foreground text-base group-hover:text-primary transition-colors">{bill.billName}</p>
                    <p className="text-[10px] font-bold text-muted-foreground uppercase">{bill.project?.projectName}</p>
                  </td>

                  <td className="p-4">
                    <div className="flex flex-col gap-1">
                      <div className="flex items-center gap-2 text-xs font-bold text-slate-500">
                        <CalendarDays size={12} /> Target: {bill.tentativeBillingDate ? format(new Date(bill.tentativeBillingDate), 'MMM dd, yyyy') : 'N/A'}
                      </div>
                      {bill.receivedDate && (
                        <div className="flex items-center gap-2 text-xs font-bold text-success">
                          <CheckCircle2 size={12} /> Rec: {format(new Date(bill.receivedDate), 'MMM dd, yyyy')}
                        </div>
                      )}
                    </div>
                  </td>

                  <td className="p-4 text-center">
                    <div className="flex flex-col items-center">
                      <span className="font-black text-slate-400 text-xs">Target: {targetPct}%</span>
                      {Number(recAmt) > 0 && <span className="font-black text-success text-xs">Realized: {recPct}%</span>}
                    </div>
                  </td>

                  <td className="p-4">
                    <div className="space-y-1">
                      <p className="font-bold text-foreground">Due: {formatCurrency(dueAmt)}</p>
                      <p className={cn("text-xs font-black", Number(recAmt) > 0 ? "text-success" : "text-muted-foreground/50")}>
                        Rec: {formatCurrency(recAmt)}
                      </p>
                    </div>
                  </td>

                  <td className="p-4">
                    <Badge className={cn(
                      "font-black text-[10px] px-2 shadow-none",
                      bill.status === 'PAID' ? "bg-success/10 text-success border-success/20" :
                        bill.status === 'PARTIAL' ? "bg-amber-500/10 text-amber-600 border-amber-500/20" : "bg-slate-100 text-slate-400"
                    )} variant="outline">
                      {bill.status}
                    </Badge>
                  </td>

                  <td className="p-4 text-right">
                    <div className="flex justify-end gap-2">
                      <RecordPaymentDialog bill={bill} onSuccess={() => window.location.reload()} />
                      <Button variant="ghost" size="icon" onClick={() => onBillClick(bill)}>
                        <ExternalLink size={14} />
                      </Button>
                    </div>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
}